
    import {Input} from '../../../../src'
    import {useRefs} from "../../../../src/use/useRefs";

    export default {
        name: "demo-use-refs",
        setup() {

            const {refs} = useRefs({
                input: HTMLInputElement,
                plInput: Input,
            })

            return {
                clearInput() {
                    console.log(refs.input)
                    refs.input.value = ''
                },
                clearPlInput() {
                    console.log(refs.plInput)
                    refs.plInput!.methods.clearValue()
                },
            }
        },
    }
